.header{
  /* border: 1px solid red; */
  display: flex;
  justify-content: center;
  /* max-width: 80%; */
}
input{
    background-color: #202c3c;
    border: 1px solid #202c3c;
    border-radius: 0.5rem;
    color: white;
    padding: 0.8rem;
    font-size:1.2rem;
    /* margin-right: 1.5rem; */
    width: 60%;
  }
  
  input:focus{
    outline: none;
    box-shadow: 0 0 0 0.1rem #202c3c;
  }

.submit{
    /* submit button */
    background-color: #202c3c;
    border: 1px solid #202c3c;
    color: white;
    font-size: 1.2rem;
    margin-left: 5px;
    padding: 0.8rem;
    border-radius: 0.4rem;
    width:10%;
}


.submit:hover{
    outline: none;
    box-shadow: 0 0 0 0.1rem #202c3c;
}
.submit1{
  display:none;
  background-color: #202c3c;
  border: 1px solid #202c3c;
  color: white;
  font-size: 1.2rem;
  margin: 5px;
  padding: 0.8rem;
  border-radius: 0.4rem;
  width:10%;
}
.submit1 img{
  -webkit-filter: invert(100%);
  filter: invert(100%);
  width:100%;
}

.forecast{
    /* forecast container */
    background-color: #202c3c;
    border-radius: 1rem;
    padding: 1rem;
}
.details{
  /* display: flex; */
  background-color: #202c3c;
  border-radius: 1rem;
  padding: 1.2rem;
}

.summary{
  grid-column: 2/3;
  grid-row: 1/4;
  background-color: #202c3c;
  border-radius: 1rem;
  padding: 1.2rem;
}

.content{
  color: #8b939f;
  width: 80%;
  display: grid;
  grid-template-columns: 70% auto;
  grid-template-rows: 10rem 12rem 12rem;
  grid-gap: 1rem;
  margin-top: 1.2rem;
}

.temp{
  font-size: 3rem;
  font-weight: 600;
  color: white;
  margin: 0;
}

.city{
  font-size: 2rem;
  font-weight: 600;
  color: white;
}

.center{
  display: flex;
  justify-content: center;
  align-items: center;
  padding-bottom: 2rem;
}

.main{
  margin-top: 2rem;
}

.forecast-item{
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  border-bottom: 1px solid #8b939f;
  height:5rem;
}

.time{
  display: flex;
  justify-content: center;
}

.temper{
  display: flex;
  justify-content: center;
  color:white;
  font-size: 1.2rem;
}

.horizontal .all-items{
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  
  

}::-webkit-scrollbar {
  display: none;
}

.today-item{
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1rem;
}

.detail-value{
  font-size: 1.2rem;
  font-weight: 600;
  color: white;
}

.items{
  /* align-items: center; */
  justify-content: center;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  grid-gap: 1rem;
  padding: 1rem;
}
.main-icon{
  grid-column: 2/3;
  grid-row: 1/4;
  transform: scale(3);
  justify-self: center;
  align-self: center;
}

.city-temp{
  display: flex;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2rem 2rem;
  grid-gap: 1rem;
  padding: 1rem;
  margin-top: -1rem;
  justify-content: space-between;
  margin-right: 3rem;
}



.labell{
  margin-top:-1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 0.5rem;
  font-weight: 600;
}

#search::-webkit-search-cancel-button{
  position:relative;


}
#search + svg {
  visibility: hidden;
}
#search:placeholder-shown + svg {
  visibility: visible;
}
#search + svg {
  margin-left: -1.95rem;
  margin-top: 0.95rem;
}
@media (max-width:1400px){
  .content{
    display: flex;
    flex-direction: column;
  }



}
@media (max-width:852px){
 .submit{
  display: none;
 }
 .submit1{
  display: block;
 }
}
@media (max-width:520px){
  .forecast-item .type{
    display: none;
  }
  /* .city{
    font-size: 2rem;
  }

  .detail-value, .detail-item{
    font-size:1rem;
  } */
  
.items{
  display: grid;

  grid-template-columns: 2fr 1fr ;
  grid-template-rows: 1fr 1fr 1fr;
}



.labell{
  display: none;
}

.main-icon{
  position: relative;
  top: 2.5rem;
  

}

}